<template>
  <v-app>
    <nav-bar></nav-bar>
    <div class="put-index">
      <el-dialog
        class="dialog"
        title="È™∏ÂÜ∞ÁßíÊùÄ"
        :visible.sync="updatecenterDialogVisible"
        width="40%"
        left
        :modal="false"
      >
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="ÁßíÊùÄÊó∂Âàª">
            <el-col :span="10">
              <el-date-picker
                type="date"
                placeholder="ÈÄâÊã©Êó•Êúü"
                v-model="form.date1"
                style="width: 100%"
              ></el-date-picker>
            </el-col>
            <el-col class="col" :span="1"></el-col>
            <el-col :span="10">
              <el-time-picker
                placeholder="ÈÄâÊã©Êó∂Èó¥"
                v-model="form.date2"
                style="width: 100%"
              ></el-time-picker>
            </el-col>
          </el-form-item>

          <el-alert
            title="ÔºàÂåÖÂê´Êú´Â∞æÊï∞Â≠óÔºåÊäòÊâ£Âå∫Èó¥0-10)"
            type="warning"
          ></el-alert>
          <el-form-item
            v-for="(domain, index) in dynamicValidateForm.domains"
            :key="domain.key"
            :label="'Ê¥ªÂä®' + (index + 1)"
            class="mt-2"
          >
            Á¨¨
            <el-input
              v-model.number="domain.start"
              type="number"
              @input="changePaidAmount($event, index)"
              class="w-65"
            />ÂêçÂà∞Á¨¨
            <el-input
              v-model.number="domain.end"
              type="number"
              @input="changePaidAmount1($event, index)"
              class="w-65"
            ></el-input
            >Âêç Êâì
            <el-input
              v-model="domain.discount"
              type="number"
              @input="changePaidAmount2($event, index)"
              class="w-40"
            ></el-input
            >Êäò
            <el-button
              @click.prevent="removeDomain(domain)"
              class="btn-none border-no btn-hover ml-8"
              style="background-color: #e64c4c; color: #fff"
              >Âà†Èô§</el-button
            >
          </el-form-item>
          <el-form-item>
            <el-button @click="addDomain">Êñ∞Â¢ûËßÑÂàô</el-button>
          </el-form-item>

          <el-form-item label="Ê¥ªÂä®‰ªãÁªç">
            <el-input type="textarea" v-model="form.desc"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              class="bg-color btn-none border-no btn-hover"
              style="color: #fff"
              @click="addSecKill"
              >Á´ãÂç≥ÂàõÂª∫</el-button
            >
            <el-button @click="dialogControl">ÂèñÊ∂à</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <div
        v-for="(item, index) in goodsInfo"
        :key="index"
        class="put-card pa-15 glassmorphism"
      >
        <div style="width: 50%" class="right mt-12">
          <h1 class="put-title">
            {{ item.goodName }}
          </h1>
          <div class="put-price">Ôø• {{ item.price }}</div>
          <div class="mt-8">{{ item.description }}</div>
          <div class="put-btn mt-12">
            <button
              @click="dialogControl(item.pkGoodId)"
              class="overlay__btn overlay__btn--colors btn-none"
            >
              <span>ÁßíÊùÄÊ¥ªÂä®</span>
              <span class="overlay__btn-emoji">üé®</span>
            </button>
          </div>
        </div>
        <img class="put-img" :src="item.image" />
      </div>
    </div>
  </v-app>
</template>

<script>
import NavBar from "../../components/NavBar";
import { hbStrategyadd } from "@/utils/request.js";
import { USER_ID } from "@/store/mutation-types";
const limit = require("@/utils/limitInputNum.js");
import Vue from "vue";
export default {
  data() {
    return {
      // startRules:[{ required: true, message: "ËØ∑ËæìÂÖ•ÂºÄÂßãÊéíÂêç",trigger: "blur" }],
      goodsInfo: [],
      updatecenterDialogVisible: false,
      form: {
        date1: "",
        date2: "",
        desc: "",
      },
      goodId: "",
      dynamicValidateForm: {
        domains: [
          {
            start: "",
            end: "",
            discount: "",
          },
        ],
      },
    };
  },
  created() {
    this.getAllGoodsByUser();
  },
  methods: {
    changePaidAmount(e, index) {
      this.dynamicValidateForm.domains[index].start = limit.limitInputNumber(
        e,
        1000,
        0,
        4
      );
      if (e > 1000) {
        this.$message({
          message: "ËØ∑ËæìÂÖ•Â∞ë‰∫é1000ÁöÑÂÄº",
          type: "warning",
        });
      }
    },
    changePaidAmount1(e, index) {
      this.dynamicValidateForm.domains[index].end = limit.limitInputNumber(
        e,
        1000,
        1,
        4
      );
      if (e > 1000) {
        this.$message({
          message: "ËØ∑ËæìÂÖ•Â∞ë‰∫é1000ÁöÑÂÄº",
          type: "warning",
        });
      }
    },
    changePaidAmount2(e, index) {
      this.dynamicValidateForm.domains[index].discount = limit.limitInputNumber(
        e,
        10,
        0,
        3
      );
      if (e > 10) {
        this.$message({
          message: "ÊúÄÂ§öÊâì10ÊäòÔºàÂç≥Âéü‰ª∑Ôºâ",
          type: "warning",
        });
      }
    },
    getAllGoodsByUser() {
      let params = new URLSearchParams();
      params.append("userId", Vue.ls.get(USER_ID));
      this.axios
        .post(this.GLOBAL.contentUrl + "/goods/findGoodsByUserId", params)
        .then((res) => {
          this.goodsInfo = res.data.data.Goods;
        });
    },
    dialogControl(goodId) {
      this.updatecenterDialogVisible = !this.updatecenterDialogVisible;
      this.goodId = goodId;
      this.getSecKillInfo();
    },
    getSecKillInfo() {
      let params = new URLSearchParams();
      params.append("goodId", this.goodId);
      this.axios
        .post(this.GLOBAL.contentUrl + "/hbStrategy/get", params)
        .then((res) => {
          let data = res.data.data;
          //Ê†πÊçÆÁ≠ñÁï•ÂºÄÂßãÁöÑÂêçÊ¨°ÂØπÊäòÊâ£ËøõË°åÊéíÂ∫è
          data.ruleDtoList.sort(this.compare("start"));
          this.goodId = data.goodId;
          this.form.date1 = data.day;
          this.form.date2 = data.detail;
          this.form.desc = data.description;
          for (let i = 0; i < data.ruleDtoList.length; i++) {
            let domain = {
              start: data.ruleDtoList[i].start,
              end: data.ruleDtoList[i].end,
              discount: data.ruleDtoList[i].discount,
            };
            this.dynamicValidateForm.domains[i] = domain;
          }
        });
    },
    compare(property) {
      return function (a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return value1 - value2;
      };
    },
    removeDomain(item) {
      var index = this.dynamicValidateForm.domains.indexOf(item);
      if (index !== -1) {
        this.dynamicValidateForm.domains.splice(index, 1);
      }
    },
    addDomain() {
      this.dynamicValidateForm.domains.push({});
    },
    addSecKill() {
      this.$confirm("Ê≠§Êìç‰ΩúÂ∞Ü‰øÆÊîπÂïÜÂìÅËé∑Âæó, ÊòØÂê¶ÁªßÁª≠?", "ÊèêÁ§∫", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        type: "warning",
      })
        .then(() => {
          let d = new Date(this.form.date1);
          let date1 =
            d.getFullYear() +
            "-" +
            (d.getMonth() + 1) +
            "-" +
            d.getDate() +
            " " +
            d.getHours() +
            ":" +
            d.getMinutes() +
            ":" +
            d.getSeconds();
          d = new Date(this.form.date2);
          let date2 =
            d.getFullYear() +
            "-" +
            (d.getMonth() + 1) +
            "-" +
            d.getDate() +
            " " +
            d.getHours() +
            ":" +
            d.getMinutes() +
            ":" +
            d.getSeconds();
          console.log(date1, date2);
          let params = {
            goodId: this.goodId,
            day: date1,
            detail: date2,
            description: this.form.desc,
            ruleDtoList: this.dynamicValidateForm.domains,
          };
          hbStrategyadd(params).then((res) => {
            this.$message({
              message: "ÂàõÂª∫ÊàêÂäü",
              type: "success",
            });
            this.updatecenterDialogVisible = !this.updatecenterDialogVisible;
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Â∑≤ÂèñÊ∂à‰øÆÊîπ",
          });
        });
    },
  },
  components: {
    NavBar,
  },
};
</script>

<style>
.dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}
.put-index {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(#d4ecec, #91c2be);
}

.put-card {
  margin: 100px auto;
  text-align: center;
  width: 83%;
  height: 530px;
}
.put-title {
  font-size: 3rem;
  width: 100%;
}
.put-img {
  width: 400px;
  height: 400px;
  float: right;
}
.put-price {
  color: #26a69a;
  font-size: 1.875rem;
  width: 100%;
}
.overlay__btn {
  width: 30%;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 600;
  color: #fff;
  background: #26a69a;
  border: none;
  border-radius: 0.5rem;
  transition: transform 150ms ease;
  outline-color: hsl(#26a69a, 95%, 50%);
}

.overlay__btn:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.overlay__btn--transparent {
  background: transparent;
  color: #26a69a;
  border: 2px solid #26a69a;
  border-width: 2px;
  margin-right: 0.75rem;
}

.overlay__btn-emoji {
  margin-left: 0.375rem;
}
.put-btn {
  display: flex;
  justify-content: center;
}
</style>
